// src/components/HomeClient.tsx
"use client";

import { useEffect, useState } from "react";
import { useAppDispatch } from "@/store/hooks";
import { authActions } from "@/store/slices/authSlice";
import FilmList from "./List";
import LandingPage from "./landing";
import LoadingSpinner from "./UI/LoadingSpinner";
import Film from "@/models/Film";

type Props = {
  user: {
    name: string;
    email: string;
  };
};

export default function HomeClient({ user }: Props) {
  const dispatch = useAppDispatch();
  const [movies, setMovies] = useState<Film[] | null>(null);

  useEffect(() => {
    dispatch(authActions.login({ user }));
  }, [dispatch, user]);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await fetch(
          "https://api.themoviedb.org/3/movie/popular?api_key=fbd1edacbe4e94f662341a99cd3be594&language=ar&page=1"
        );
        const data = await response.json();
        setMovies(data.results);
      } catch (error) {
        console.error("Fetch error:", error);
      }
    }

    fetchData();
  }, []);

  if (!movies) {
    return (
      <div className="flex justify-center items-center h-screen">
        <LoadingSpinner />
      </div>
    );
  }

  const array_one = movies.slice(0, 5);
  const array_two = movies.slice(5, 10);
  const array_three = movies.slice(10, 15);
  const array_four = movies.slice(15, 20);

  return (
    <>
      <LandingPage />
      <FilmList title="Popular on Netflix" films={array_one} />
      <div className="mt-24">
        <FilmList title="Action" films={array_two} />
      </div>
      <div className="mt-24">
        <FilmList title="Romantic" films={array_three} />
      </div>
      <div className="mt-24">
        <FilmList title="Hard" films={array_four} />
      </div>
    </>
  );
}
